    USAGE_UPDATE_BACKUP_HEADER: "Mettre  jour/Synchroniser une sauvegarde",
    USAGE_UPDATE_BACKUP_CONTENT: "<p>Si auparavant vous avez dj cr une sauvegarde de l'emplacement source slctionn vous pouvez la synchroniser avec ce dernier sans avoir  recrer de nouveau la sauvegarde ce qui peut tre coteux en termes de temps et de l'espace disque.</p><p>Backy comparera tous les lments dans les emplacements source et destination et copiera uniquement ceux qui ont t changs depuis la dernire sauvegarde. Vous pouvez choisir parmi deux critres de comparaison. Soit la version la plus rcente soit la plus volumineuse (en termes de la taille de fichier en octets) sera conserve.</p><p>De plus, vous pouvez dcider si les fichiers qui existent dans l'emplacement destinataire doivent tre conservs mme si ces derniers n'existent plus dans l'emplacement source. Par dfaut, de tels fichiers sont suppprims. Vous pouvez dsactiver ceci en dcochant l'option <strong>Conserver les fichiers obsoltes</strong>.</p><p>Pour dmarrer le processus de sauvegarde cliquez sur le bouton <strong>Lancer la sauvegarde</strong>.</p>",
