    ABOUT_INTRODUCTION_HEADER: "o je Backy?",
    ABOUT_INTRODUCTION_CONTENT: "<p>Backy je jednoduch rieenie na vytvranie a sprvu zloh s otvorenm zdrojovm kdom. Vemi jednoducho sa pouva je kompatibiln s operanmi systmami Linux aj Windows.</p><p>Backy umouje vytvra zlohy prieinkov alebo celch diskovch jednotiek. Potom je mon vytvoren zlohu aktualizova zakadm ke sa obsah zdrojovho umiestnenia zmen.</p>",
    ABOUT_AUTHOR_HEADER: "Tvorca",
    ABOUT_AUTHOR_CONTENT: "<p>Tento program napsal <a href='mailto:marek.felsoci@gmail.com'>Marek Felci</a>, IT tudent <a href='http://www.unistra.fr' target='_blank'>trasburskej univerzity vo Franczsku</a>.",
    ABOUT_CONTACT_HEADER: "Mte nejak otzky, pripomienky alebo chu sa podiea na tomto projekte?",
