    ABOUT_LICENSE_HEADER: "Licencia",
    ABOUT_LICENSE_CONTENT: "<p>Backy je ren pod licenciou <a href='https://www.gnu.org/licenses/gpl-3.0.en.html' target='_blank'>GNU General Public License version 3.0</a>.</p>",
    USAGE_INTRODUCTION_HEADER: "Ako pouva Backyho?",
    USAGE_INTRODUCTION_CONTENT: "<p>Grafick pouvatesk rozhranie Backyho je znane intuitvne najm pre zaiatonkov.</p>",
    USAGE_CREATE_BACKUP_HEADER: "Vytvori zlohu",
    USAGE_CREATE_BACKUP_CONTENT: "<p>Pre vytvorenie zlohy muste zada cestu k umiestneniu obsahu, ktor chcete zlohova a zrove aj cestu k umiestneniu, kde m by zloha vytvoren. Potom odznate monos <strong>Iba aktualizova</strong> pre vytvorenie plne novej zlohy. Nakoniec kliknite na tlaidlo <strong>Zlohova</strong> pre spustenie procesu zlohovania a akajte km sa nedokon.</p>",
    USAGE_UPDATE_BACKUP_HEADER: "Aktualizova/Synchronizova zlohu",
    USAGE_UPDATE_BACKUP_CONTENT: "<p>Ak u mte vytvoren zlohu uvedenho zdrojovho umiestnenia, mete ju s nm jednoducho synchronizova bez toho, aby ste museli znova vytvra aliu zlohu o me by nkladn o sa tka asu a miesta na disku.</p><p>Backy porovn vetky poloky v zdrojovom a cieovom umiestnen a skopruje len tie, ktor boli zmenen od poslednho zlohovania. Mete si vybra spomedzi dvoch porovnvacch kritri. Bu sa zachovaj tie najnovie alebo tie najvie (tzn. vekos sboru v bajtoch) verzie sborov.</p><p>Mete si vybra aj to, i sa sbory, ktor stle jestvuj v cieovom umiestnen ale u sa nenachdzaj v zdrojovom umiestnen zlohy, odstrnia alebo bud zachovan. Predvolene sa takto sbory odstrauj. Toto nastavenie mete zmeni tm, e oznate monos <strong>Zachova nepotrebn sbory</strong>.</p><p>Pre spustenie zlohovania kliknite na tlaidlo <strong>Zlohova</strong>.</p>",
    USAGE_ABORT_BACKUP_HEADER: "Prerui prebiehajce zlohovanie",
    USAGE_ABORT_BACKUP_CONTENT: "<p>V prpade, e chcete prerui prebiehajci proces zlohovania kliknite na tlaidlo <strong>Prerui</strong>. Buete poiadan o potvrdenie vaej voby. Vyberte monos <strong>no</strong> ak ste si ist, e chcete prerui zlohovanie.</p><p>Vedzte, e proces zlohovania nebude preruen okamite! Najprv bude dokonen spracovanie aktulnej poloky.</p>",
