    USAGE_CHANGE_DISPLAY_LANGUAGE_CONTENT: "<p>Pre zmenu jazyka prejdite do okna <strong>Predvoby</strong> cez hlavn ponuku programu alebo pomocou klvesovej skratky <strong>Shift+Ctrl+P</strong>, vyberte poadovan jazyk a kliknite na tlaidlo <strong>Uloi</strong>.</p>",
    USAGE_MAXIMAL_COPY_BUFFER_SIZE_HEADER: "Zmeni maximlnu vekos zsobnka koprovania",
    USAGE_MAXIMAL_COPY_BUFFER_SIZE_CONTENT: "<p>Okno <strong>Predvoby</strong> umouje rune nastavi najviu mon vekos zsobnka koprovania. Tto hodnota neme prekroi vekos pamte RAM vho potaa, ktor je tie zobrazen v okne <strong>Predvoby</strong>. Pretoe nastavenie zlej hodnoty me spsobi pd programu alebo dokonca aj celho systmu, neodporame vm tto hodnotu meni ak si nie ste plne ist tm, o robte!</p>",
